@import url(https://s3.eu-central-1.amazonaws.com/dentolo-ui-elements/dist/css/main.css?v=1.4);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.user-list-item {
 cursor: pointer;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 200ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 200ms ease-in;
}
.users-container {
    min-height: 350px;
    padding: 16px;
}
.user-details {
    margin-bottom: 16px;
}
.user-details-modal {
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    background-color: rgba(0, 50, 100, 0.7 );
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.modal-content {
    background-color: white;
    padding: 16px;
    display: flex;
    flex-direction: column;
}
.header-wrapper {
    background-color: white;
    min-height: 86px;
    font-size: 16px;
    padding: 16px;
    width: 100%;
    position: fixed;
    top: 0;
    box-shadow: 0 0.125rem 0.25rem 0 rgba(0,50,100,0.14),0 0.1875rem 0.25rem 0 rgba(0,50,100,0.12),0 0.0625rem 0.3125rem 0 rgba(0,50,100,0.2) !important;
    z-index: 100;
}
.search-container {
    width: 100%;
    height: 85vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.search-container__title {
    margin: 32px 0px;
}
